import { Card, Container, Row, Col } from "react-bootstrap";
import styled from "styled-components";
import axios from "axios";
import { useQuery } from "@tanstack/react-query";

const CardDiv = styled.div`
  z-index: 100;
  position: absolute;
  width: 20%;
  left: 10px;
  top: 90px;
  pointer-events: default;
`;

const Requests = (props) => {
  const { data: icons } = useQuery(
    ["icons"],
    async () => {
      const { data } = await axios.get(
        "https://www.geelong.stantec.online/v1/query/requests?schema=nzta&columns=st_x(point)%20as%20x%2C%20st_y(point)%20as%20y%2Ccontrols%2Crequest_id%2Csh_start%2Csh_end%2Crs_start%2Crs_end%2Crp_start%2Crp_end%2Clocation_notes%2Ccontractor%2Cwork_class%2Cdescription"
      );
      return data;
    })
  const { flyFunction } = props;

  const handleClick = (r) => flyFunction(r)
  return (
    <CardDiv>
      <Card variant="dark">
        <Card.Header style={{ background: "orange", fontWeight: "bold"}}>Active Corridor Requests<p style={{ fontSize: "0.5rem", marginBottom: "2px"}}>Click to Fly to Site</p></Card.Header>
        
        <div style={{ height: '700px', overflowY: 'scroll', overflowX: 'hidden'}}>
            
        <Card.Body >
        
          <Container>
          <hr style={{ margin: "2px" }} />
            {icons && icons.map((r) => (
              <>
             
              <Row style={{ lineHeight: "0.85"}} key={`list-request-${r.request_id}`} onClick={(e) => handleClick({x: r.x, y: r.y})}>
                <Col md={3}>
                    <img src={process.env.PUBLIC_URL + `/assets/icons/${r.controls}`} height={54} width={54} alt={"road sign"} />
                </Col>
                <Col md={9}>
                <span style={{ whiteSpace: "nowrap",  fontSize: "0.7rem", fontWeight: "bold"}}>{r.location_notes}</span><br/>
                <span style={{ fontSize: "0.6rem"}}>{r.description}</span><br/>
                <span style={{ fontSize: "0.6rem", fontWeight: "bold"}}>{`${r.sh_start}-${String(r.rs_start).padStart(4,'0')}/${r.rp_start.toFixed(2)}`} to {`${r.sh_end}-${String(r.rs_end).padStart(4,'0')}/${r.rp_end.toFixed(2)}`}</span>
                </Col>
              </Row>
              <hr style={{ margin: "4px" }} />
              </>
            ))}
            
          </Container>
          
        </Card.Body>
        </div>
      </Card>
    </CardDiv>
  );
};

export default Requests;
