import DeckGL, {FlyToInterpolator} from "deck.gl";
import { Map, NavigationControl } from "react-map-gl";
import { useQuery } from "@tanstack/react-query";
import { GeoJsonLayer, IconLayer } from "@deck.gl/layers";
import axios from "axios";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import { useState } from "react";
import styled from "styled-components";
import Requests from "./Requests";

const MAPBOX_ACCESS_TOKEN =
  "pk.eyJ1IjoicGV0ZWhpbGxqbnIiLCJhIjoiY2swcjZtd3IxMDJjOTNjb3c1Z25wczQ3NyJ9.2Ut4kmGeVuvc7UUb-qezNw";

const MAP_STYLE = "mapbox://styles/mapbox/light-v9";

const INITIAL_VIEW_STATE = {
  longitude: 169.2,
  latitude: -45.13,
  zoom: 10,
  pitch: 60,
  bearing: 0,
};

const InfoBox = styled.div`
  font-size: 0.8rem;
  border-radius: 6px;
  max-width: 350px;
  min-width: 250px;
  border-width: 3px;
  border-style: solid;
  border-color: black;
  background: orange;
  color: black;
  padding: 5px;
  z-index: 1000;
  opacity: 1;
  vertical-align: top;
`;
const toastId_sections = toast.info("Fetching data ...");

function renderTooltip(info) {
  if (!info) {
    return null;
  }
  return (
    <InfoBox>
      <table>
        <tbody>
          <tr style={{ verticalAlign: "top" }}>
            <td>
              <strong>Description:</strong>
            </td>
            <td>
              {"  "}
              {info.description}
            </td>
          </tr>
          <tr style={{ verticalAlign: "top" }}>
            <td>
              <strong>Contractor:</strong>
            </td>
            <td>
              {"  "}
              {info.contractor}
            </td>
          </tr>
          <tr style={{ verticalAlign: "top" }}>
            <td>
              <strong>Type:</strong>
            </td>
            <td>
              {"  "}
              {info.work_class}
            </td>
          </tr>
          <tr style={{ verticalAlign: "top" }}>
            <td>
              <strong>Location:</strong>
            </td>
            <td>
              {"  "}
              {info.location_notes}
            </td>
          </tr>
          <tr style={{ verticalAlign: "top" }}>
            <td>
              <strong>Start:</strong>
            </td>
            <td>
              {"  "}
              {` ${info.sh_start}-${String(info.rs_start).padStart(4, "0")} / ${
                info.rp_start
              }`}
            </td>
          </tr>
          <tr style={{ verticalAlign: "top" }}>
            <td>
              <strong>End:</strong>
            </td>
            <td>
              {"  "}
              {` ${info.sh_end}-${String(info.rs_end).padStart(4, "0")} / ${
                info.rp_end
              }`}
            </td>
          </tr>
        </tbody>
      </table>
    </InfoBox>
  );
}

const MapHolder = (props) => {
  const [hoverInfo, setHoverInfo] = useState(null);
  const [initialViewState, setInitialViewState] = useState(INITIAL_VIEW_STATE);

  const flyToSite = (coords) => {
    setInitialViewState({...initialViewState, latitude: coords.y, longitude: coords.x, zoom: 15,
      transitionDuration: 3000,
      transitionInterpolator: new FlyToInterpolator() })
  }

  const { data: sections, isFetching: fetchingSections } = useQuery(
    ["sections"],
    async () => {
      const { data } = await axios.get(
        "https://www.geelong.stantec.online/v1/geojson/nzta.sections?geom_column=geom&columns=source%2C%20target&id_column=id&precision=5"
      );
      return data;
    },
    {
      onError: (error) =>
        toast.error(
          `Something went wrong loading the State Highway data: ${error.message}`
        ),
      staleTime: 1000 * 60 * 60,
    }
  );

  const { data: requests, isFetching: fetchingRequests } = useQuery(
    ["requests"],
    async () => {
      const { data } = await axios.get(
        "https://www.geelong.stantec.online/v1/geojson/nzta.requests?geom_column=geom&columns=request_id&precision=5"
      );
      return data;
    },
    {
      onError: (error) =>
        toast.error(
          `Something went wrong loading the CAR data: ${error.message}`
        ),
      staleTime: 1000 * 60,
    }
  );

  const { data: icons, isFetching: fetchingIcons } = useQuery(
    ["icons"],
    async () => {
      const { data } = await axios.get(
        "https://www.geelong.stantec.online/v1/query/requests?schema=nzta&columns=st_x(point)%20as%20x%2C%20st_y(point)%20as%20y%2Ccontrols%2Crequest_id%2Csh_start%2Csh_end%2Crs_start%2Crs_end%2Crp_start%2Crp_end%2Clocation_notes%2Ccontractor%2Cwork_class%2Cdescription"
      );
      return data;
    },
    {
      onError: (error) =>
        toast.error(
          `Something went wrong loading the Icon data: ${error.message}`
        ),
      staleTime: 1000 * 60,
    }
  );

  if (!fetchingSections && !fetchingIcons && !fetchingRequests)
    toast.dismiss(toastId_sections);

  const layers = [
    new GeoJsonLayer({
      id: "geojson",
      data: sections,
      stroked: false,
      filled: false,
      autoHighlight: true,
      pickable: false,
      lineWidthMinPixels: 4,
      getLineWidth: 4,
      getLineColor: [0, 0, 255, 40],
      parameters: {
        depthTest: false,
      },
    }),
    new GeoJsonLayer({
      id: "geojson_requests",
      data: requests,
      stroked: false,
      filled: false,
      autoHighlight: true,
      pickable: true,
      lineWidthMinPixels: 8,
      getLineWidth: 20,
      getLineColor: [255, 0, 0, 125],
      parameters: {
        depthTest: false,
      },
      onHover: (info) => {
        if (info.object) {
          const infoData = icons.filter(
            (d) => d.request_id === info.object.properties.request_id
          );

          setHoverInfo({ ...infoData[0], posX: info.x, posY: info.y });
        } else {
          setHoverInfo(null);
        }
      },
    }),
    new IconLayer({
      id: "icon-layer",
      data: icons,
      getIcon: (d) => ({
        url: process.env.PUBLIC_URL + `/assets/icons/${d.controls}`,
        width: 128,
        height: 128,
      }),
      getPosition: (d) => [d.x, d.y, 20],
      sizeScale: 15,
      pickable: true,
      sizeUnits: "meters",
      sizeMinPixels: 30,
      sizeMaxPixels: 128,
      parameters: {
        depthTest: false,
      },
      onHover: (info) => {
        if (info.object) {
          const infoData = icons.filter(
            (d) => d.request_id === info.object.request_id
          );
          setHoverInfo({ ...infoData[0], posX: info.x, posY: info.y });
        } else {
          setHoverInfo(null);
        }
      },
    }),
  ];

  return (
    <div>
      <Requests flyFunction={flyToSite}/>
      <DeckGL
        layers={layers}
        initialViewState={initialViewState}
        controller={true}
      >
        <Map mapboxAccessToken={MAPBOX_ACCESS_TOKEN} mapStyle={MAP_STYLE}>
          <NavigationControl
            showZoom={true}
            showCompass={true}
            visualizePitch={true}
            position="bottom-right"
          />
        </Map>

        <ToastContainer position="bottom-left" />
        {hoverInfo && (
          <div
            style={{
              position: "absolute",
              zIndex: 100,
              pointerEvents: "none",
              left: hoverInfo.posX,
              top: hoverInfo.posY,
            }}
          >
            {renderTooltip(hoverInfo)}
          </div>
        )}
      </DeckGL>
    </div>
  );
};

export default MapHolder;
